.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  background-color: #353535e6;
  color: white;
  height: 5.7rem
}

.navbar-title {
  font-size: 2.1rem;
  font-weight: bold;
  color: rgb(255, 212, 133);
  cursor: pointer;
  padding: 1.1rem;
}

.navbar-title:hover{
  color: rgb(254, 198, 93);
}

.navbar-right {
  display: flex;
  align-items: center;
}

.search-box {
  position: relative;
  margin-right: 2.3rem;
}

.search-box input[type="text"] {
  width: 270px;
  padding: 5px;
  border: none;
  border-radius: .63rem;
  height: 2.3rem;
  font-size: 23px;
  outline: none;
}

.search-button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 3px;
  cursor: pointer;
  outline: none;

}

.action-button{
  cursor: pointer;
  font-size: 40px;
  margin-right: 19px;
  margin-left: 10px;
  background-color: #353535e6;
}
 